import Vue from 'vue'
import Router from 'vue-router'
import PsnWkPlan from '@/components/psnWkPlan'
import Test from '@/components/test'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/psnWkPlan',
    component: PsnWkPlan
  }, {
    path: '/test',
    component: Test
  }]
})



